* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 45px;
  color: aliceblue;
  font-weight: 600;
  text-shadow: 2px 2px 2px black;
}
header {
  text-align: center;
  padding: 4px 10%;
  margin-bottom: 50px;
  background-color: rgb(102, 94, 74);
}

.app {
  background: url(../src/img/background.jpg);
  background-repeat: no-repeat;
  height: 100vh;
}

.taskCard {
  width: 250px;
  padding: 10px 25px;
  margin: 10px 1%;
  background-color: rgb(228, 228, 228);
  border-radius: 5px;
}

.taskCardTitleInputArea {
  margin-bottom: 10px;
  width: 30%;
  cursor: pointer;
}

.taskCardTitleInput {
  width: 200px;
  font-size: 1.1rem;
  padding: 4px 6px;
  border-radius: 3px;
  border: none;
  outline: none;
}

.taskAddInput {
  width: 100%;
  font-size: 1.3rem;
  padding: 10px 15px;
  border-radius: 3px;
  border: none;
  outline: none;
}

.taskBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 17px 0;
  margin-top: 10px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgb(75, 75, 75);
}

.taskText{
  margin-left:12px;
}
.taskTrashButton{
  cursor:pointer;
  margin-right:9px;
  border:none;
}

.taskCardsArea{
display:flex;
justify-content: flex-start;
align-items: flex-start;
flex-wrap: wrap;
}

.addTaskCardButtonArea{
  margin-left:1%;
  margin-top:25px;
}
.addTaskCardButton{
  padding:10px 18px;
  border-radius:999px;
  font-size: 30px;
  background-color: rgb(221,194,162);
  box-shadow:3px 3px 8px 1px black;
  cursor:pointer;
  transition:all 0.3s;
}

.addTaskCardButton:hover{
  box-shadow: none;
  transform:translate(3px,3px);
}

.taskCardTitleAndTaskardDeleteButtonArea{
  display: flex;
  justify-content: space-between;
}

.taskCardDeleteButton{
  border:none;
  background-color: rgb(228, 228, 228);
  cursor:pointer;
  color:rgb(158,46,46);
}